.SideDrawer {
	position: fixed;
	width: 150px;
	max-width: 70%;
	height: 100%;
	left: 0;
	top: 0;
	z-index: 200;
	background-color: #ff0055;
	/*padding: 32px 16px;*/
    padding-top: 50px;
	box-shadow: border-box;
	transition: transform 0.3s ease-out;
}

.Open {
	transform: translateX(0);
}

.Close {
	transform: translateX(-100%);
}

@media (min-width: 500px){
	.SideDrawer {
		display: none;
	}
}

